import React from 'react'
import styles from './HomePage.module.css'
import { AnimatePresence, motion } from 'framer-motion/dist/framer-motion'
import DownArrow from './DownArrow'

export default function HomePage() {
  const line1 = 'Hi I am FullStack Web Developer'
  const line2 = 'My stack is MERN Mongo DB, ExpressJS, ReactJs, NodeJs'

  const sentence = {
    hidden: {opacity: 1},
    visible: {
      opacity: 1,
      transition: {
        delay: 1,
        staggerChildren: 0.08,
      }
    }
  }
  const letters = {
    hidden: {opacity: 0, y: 50},
    visible: {
      opacity: 1,
      y: 0,
    }
  }
  return (
    <div
      className={styles.container}
      id="intro"
      style={{ backgroundImage: 'url(images/bg2.jpg)' }}
    >
       <motion.h1
        initial={{ y: '100px', opacity: 0, scale: 0.9 }}
        animate={{ y: 0, opacity: 1, scale: 1 }}
        transition={{ type: 'ease-in-out', duration: 1, bounce: 0 }}
      >
        Vishwas Paikra
      </motion.h1>
      <motion.h3
        variants={sentence}
        initial={'hidden'}
        whileInView={'visible'}
      >
        {line1.split('').map((char, idx) => {
          return (
            <motion.span key={char + '_' + idx} variants={letters}>
              {char}
            </motion.span>
          )
        })}
        <br />
        {line2.split('').map((char, idx) => {
          return (
            <motion.span key={char + '_' + idx} variants={letters}>
              {char}
            </motion.span>
          )
        })}
      </motion.h3>
      <DownArrow />
    </div>
  )
}
